<template>
  <div class="container">
    <div class="row">
      <div class="card col s6 offset-s3">
        <form class="card-content">
          <div class="row">
            <div class="input-field col s12">
              <div>
              <gmap-autocomplete
              @place_changed="setPlace">
              </gmap-autocomplete>
              <button @click="addMarker">增加標記</button>
              <button @click="computedistance">計算距離</button>
              <button @click="clearall">全部清除</button>
              </div>
            </div>

            <div class="card col s6 offset-s3"  v-if="hasGeo">
              <div class="card-action">
                <p class="grey-text">名稱: {{location.name}}</p>
                <p class="grey-text">緯度: {{center.lat}}</p>
                <p class="grey-text">經度: {{center.lng}}</p>
                <p class="grey-text">直線距離(公里): {{ distance }}</p>
              </div>
            </div>

            <!--顯示錯誤訊息-->
            <!-- <p v-if="errormsg" class="center red-text">{{errormsg}}</p>
            <div class="input-field col s12 center">
              <button type="submit" class="btn waves-light waves-effect">submit</button>
            </div> -->
          </div>
        </form>
      </div>

      <gmap-map
      ref="mapref"
      :center="center"
      :zoom="16"
      :options="options"
      style="width: 100vw;  height: 450px;"
      >

      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :icon="icon"
        @click="center=m.position"
      ></gmap-marker>

        <gmap-polyline
        :path="paths"
        :options="polylineOption"
        editable
        >
        </gmap-polyline>

      </gmap-map>

    </div>

      <hr>

      <table class="table">
        <thead>
          <tr>
            <th>地址名稱</th>
            <th>緯度</th>
            <th>經度</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(point) in paths" :key="point.name">
            <td>{{point.name}}</td>
            <td>{{point.lat}}</td>
            <td>{{point.lng}}</td>
            <td><button @click="deleteMaker(point)">刪除</button></td>
          </tr>
        </tbody>
        <tfoot>
        </tfoot>
      </table>

      <hr>

    <div class="row">
        <div class="co">
          <h5 class="d-inline-block">夜間模式：</h5>
          <div class="btn-group btn-group-toggle" data-toggle="buttons">
            <label class="btn btn-info" :class="{ active: nightMode === 'open' }">
              <input type="radio" v-model="nightMode" value="open"> 開啟
            </label>
            <label class="btn btn-info" :class="{ active: nightMode === 'close' }">
              <input type="radio" v-model="nightMode" value="close"> 關閉
            </label>
          </div>
        </div>
    </div>

    <hr>

    <div class="row">
      <div class="co">
        <h5 class="d-inline-block">隱藏商家：</h5>
        <div class="btn-group btn-group-toggle" data-toggle="buttons">
          <label class="btn btn-info" :class="{ active: hideBusinessmode == 'hide' }">
            <input type="radio" v-model="hideBusinessmode" value="hide"> 隱藏
          </label>
          <label class="btn btn-info" :class="{ active: hideBusinessmode == 'show' }">
            <input type="radio" v-model="hideBusinessmode" value="show"> 顯示
          </label>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { gmapApi } from 'vue2-google-maps'
// import google from 'google'

export default {
  name: 'Home',
  props: {
  },
  data () {
    return {
      center: { name: '', lat: 25.033710, lng: 121.564718 },
      // icon: { url: require('../assets/img/star.png') },
      markers: [],
      places: [],
      paths: [],
      distance: 0,
      //  顯示資訊
      location: {
        name: '',
        lat: '',
        lng: ''
      },
      // 顯示地點
      currentPlace: null,
      // Map 設定
      options: {
        zoomControl: true,
        mapTypeControl: true,
        scaleControl: true,
        streetViewControl: true,
        rotateControl: true,
        fullscreenControl: true
      },
      hideBusinessmode: 'show',
      nightMode: 'close',
      // 折線設定
      polylineOption: {
        strokeColor: '#008000',
        strokeWeight: '5',
        strokeOpacity: '0.5'
      },
      // 存放目前是否已獲得座標的狀態
      hasGeo: false
    }
  },
  mounted () {
    this.geolocate()
  },
  methods: {
    // gmap-autocomplete 取得 place 資訊 geometry 物件
    setPlace (place) {
      this.currentPlace = place
    },
    // 標記及經緯度查詢
    addMarker () {
      if (this.currentPlace) {
        // console.log(this.currentPlace)
        this.hasGeo = true
        const marker = {
          name: this.currentPlace.name,
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        }
        this.paths.push(marker)
        // console.log(this.paths)
        // this.location.name = this.currentPlace.formatted_address
        this.center.name = this.location.name
        this.markers.push({ position: marker })
        this.places.push(this.currentPlace)
        // 中心設為標記點
        this.center = marker
        this.location.name = this.currentPlace.name
        this.location.lat = this.center.lat
        this.location.lng = this.center.lng
        this.currentPlace = null
      }
    },
    // 計算多點直線距離
    computedistance () {
      // console.log(this.paths)
      let dis = 0
      for (let i = 0; i <= this.paths.length; i++) {
        const lat1 = ((Math.PI).toFixed(2) / 180) * this.paths[i].lat
        const lng1 = ((Math.PI).toFixed(2) / 180) * this.paths[i].lng
        const lat2 = ((Math.PI).toFixed(2) / 180) * this.paths[i + 1].lat
        const lng2 = ((Math.PI).toFixed(2) / 180) * this.paths[i + 1].lng
        // console.log(lat1, lng1, lat2, lng2)
        const R = 6371

        let d = Math.acos(Math.sin(lat1) * Math.sin(lat2) + Math.cos(lat1) * Math.cos(lat2) * Math.cos(lng2 - lng1)) * R
        dis += d
        this.distance = dis.toFixed(2)
      }
    },
    clearall () {
      this.center = { name: '', lat: 25.033710, lng: 121.564718 }
      this.path = []
      this.markers = []
      this.places = []
      this.path = []
      this.distance = 0
      // 顯示地點
      this.currentPlace = null
      this.hasGeo = false
    },
    deleteMaker (point) {
      // console.log([point])
      let delMaker = point
      this.paths.splice(this.paths.findIndex(item => item.name === delMaker.name), 1)
      this.markers.splice(this.markers.findIndex(item => item.name === delMaker.name), 1)
    },
    // 定位目前位置
    geolocate () {
      // 取得使用者目前的位置 navigator.geolocation 物件的 getCurrentPosition() 方法
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
        // console.log(position)
          this.center = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          }
          this.location.lat = this.center.lat
          this.location.lng = this.center.lng
          this.hasGeo = true
        })
      } else {
        alert('未允許或遭遇錯誤！')
      }
    }
  },
  watch: {
    // 夜間模式切換
    nightMode: function (val) {
      // console.log(val)
      if (val === 'open') {
        this.options = {
          styles: [
            { elementType: 'geometry', stylers: [{ color: '#242f3e' }] },
            { elementType: 'labels.text.stroke', stylers: [{ color: '#242f3e' }] },
            { elementType: 'labels.text.fill', stylers: [{ color: '#746855' }] },
            {
              featureType: 'administrative.locality',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#d59563' }]
            },
            {
              featureType: 'poi',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#d59563' }]
            },
            {
              featureType: 'poi.park',
              elementType: 'geometry',
              stylers: [{ color: '#263c3f' }]
            },
            {
              featureType: 'poi.park',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#6b9a76' }]
            },
            {
              featureType: 'road',
              elementType: 'geometry',
              stylers: [{ color: '#38414e' }]
            },
            {
              featureType: 'road',
              elementType: 'geometry.stroke',
              stylers: [{ color: '#212a37' }]
            },
            {
              featureType: 'road',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#9ca5b3' }]
            },
            {
              featureType: 'road.highway',
              elementType: 'geometry',
              stylers: [{ color: '#746855' }]
            },
            {
              featureType: 'road.highway',
              elementType: 'geometry.stroke',
              stylers: [{ color: '#1f2835' }]
            },
            {
              featureType: 'road.highway',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#f3d19c' }]
            },
            {
              featureType: 'transit',
              elementType: 'geometry',
              stylers: [{ color: '#2f3948' }]
            },
            {
              featureType: 'transit.station',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#d59563' }]
            },
            {
              featureType: 'water',
              elementType: 'geometry',
              stylers: [{ color: '#17263c' }]
            },
            {
              featureType: 'water',
              elementType: 'labels.text.fill',
              stylers: [{ color: '#515c6d' }]
            },
            {
              featureType: 'water',
              elementType: 'labels.text.stroke',
              stylers: [{ color: '#17263c' }]
            }
          ]
        }
      } else {
        this.options = {
          styles: []
        }
      }
    },
    // 商家名稱切換
    hideBusinessmode: function (val) {
      if (val === 'hide') {
        this.options = {
          styles: [
            {
              featureType: 'poi',
              stylers: [{ visibility: 'off' }]
            },
            {
              featureType: 'landscape',
              stylers: [{ visibility: 'off' }]
            }
          ]
        }
      } else {
        this.options = {
          styles: []
        }
      }
    }
  },
  computed: {
    google: gmapApi
  }
}
</script>

<style>

.table {
  border:1px solid #000;
  font-family: 微軟正黑體;
  font-size:16px;
  border-collapse:collapse;
}
th {
  background-color: #009FCC;
  padding:10px;
  border:1px solid #000;
  color:#fff;
